html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

.body {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #f3d4e7;
  color: #121232;
  font-family: "Lato", sans-serif;
  min-height: 100vh;
  margin: 4px; }

.form__actions {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.form__button {
  border: 1px solid #121232;
  min-width: 200px;
  height: 48px;
  padding: 16px;
  border-radius: 4px;
  font-weight: 600px;
  letter-spacing: 0.5px;
  transition: transform 1s ease-in-out;
  outline-color: violet; }
  .form__button--load {
    color: #121232;
    background-color: #ffffff;
    transition: background 0.3s ease-in-out; }
    .form__button--load:hover {
      background-color: #d3d4d8;
      transform: scale(1.05) translateX(8px); }
  .form__button--save {
    color: #ffffff;
    background-color: #a971b6;
    transition: background 0.3s ease-in-out; }
    .form__button--save:hover {
      background-color: #945d98;
      transform: scale(1.05) translateX(-8px); }

.form__label {
  display: block;
  font-size: 14px;
  margin-bottom: 8px; }

.form__textarea {
  border: 1px solid #2d2241;
  border-radius: 4px;
  background: lavenderblush;
  padding: 12px;
  resize: none;
  width: 100%;
  min-height: 350px;
  margin-bottom: 8px;
  outline-color: violet; }

.header {
  font-weight: 600px;
  font-size: 44px;
  margin: 0 0 48px 0; }

.main {
  max-width: 544px;
  width: 100%;
  border-radius: 16px;
  border: 1px solid #121232;
  background-color: #f1e7f3;
  padding: 16px;
  margin: 10px; }

.logo {
  margin: 100px;
  min-width: 100px;
  max-width: 300px; }

@media only screen and (max-width: 765px) {
  .body {
    flex-direction: column; }
  .form__actions {
    display: flex;
    flex-direction: column; }
  .form__button {
    margin: 5px; }
    .form__button--load {
      transition: background 0.3s ease-in-out; }
      .form__button--load:hover {
        background-color: #d3d4d8;
        transform: scale(1.05); }
    .form__button--save {
      color: #ffffff;
      background-color: #a971b6;
      transition: background 0.3s ease-in-out; }
      .form__button--save:hover {
        background-color: #945d98;
        transform: scale(1.05); } }
